<template>
  <header>
    <div class="l-content">
      <el-button
        @click="handleMenu"
        plain
        icon="el-icon-menu"
        size="mini"
      ></el-button>
      <!-- <h3 style="color: #fff">首页</h3> -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item
          v-for="item in tags"
          :key="item.path"
          :to="{ path: item.path }"
          >{{ item.label }}</el-breadcrumb-item
        >
      </el-breadcrumb>
    </div>
    <div class="r-content">
      <el-dropdown trigger="click" size="mini">
        <span>
          <img class="user" :src="userInfo.avatar" />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="showPersonality"
            >个人中心</el-dropdown-item
          >
          <el-dropdown-item @click.native="logOut">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

      <!-- 个人信息弹窗
      <el-dialog
        title="修改个人信息"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose"
      >
        <div class="userInfo-box">
          <div class="image"><img :src="userImg" class="avaterImage" /></div>
          <div>姓名: {{ userInfo.memberName }}</div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false"
            >确 定</el-button
          >
        </span>
      </el-dialog> -->
    </div>
  </header>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "CommonHeader",
  data() {
    return {
      userInfo: {},
      userImg: require("../assets/images/user.png"),
      dialogVisible: false,
    };
  },
  methods: {
    showPersonality() {
      this.dialogVisible = true;
    },
    handleMenu() {
      this.$store.commit("collapseMenu");
    },
    logOut() {
      this.$http
        .get("courseSystem/auth/logout", {
          headers: {
            token: this.$store.state.user.token,
          },
        })
        .then(({ data }) => {
          if (data.code == 0) {
            this.$store.commit("clearToken");
            this.$store.commit("clearMenu");
            this.$router.push("/login");
          } else {
            this.$message("退出登录失败");
          }
        });
    },
  },
  computed: {
    ...mapState({
      tags: (state) => state.tab.tabsList,
    }),
  },
  created() {
    this.$store.commit("getUserInfo");
    this.userInfo = this.$store.state.user.userInfo;
    if (this.userInfo.role === "teacher") {
      this.userInfo.role = "老师";
    }
    if (this.userInfo.role === "admin") {
      this.userInfo.role = "管理员";
    }
  },
};
</script>
<style lang="less" scoped>
.userInfo-box {
  .image {
    display: flex;
    justify-content: center;
    .avaterImage {
      width: 100px;
      height: 100px;
      border-radius: 50%;
    }
  }
}
.dialog-footer {
  display: flex;
  justify-content: center;
}
header {
  display: flex;
  height: 100%;
  justify-content: space-between;
  align-items: center;
}
.l-content {
  display: flex;
  align-items: center;
  .el-button {
    margin-right: 20px;
  }
}
.r-content {
  .user {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
}
/deep/ .el-breadcrumb__item {
  .el-breadcrumb__inner {
    font-weight: normal;
    &.is-link {
      color: #666;
    }
    a {
      color: #666;
    }
  }
  &:last-child {
    .el-breadcrumb__inner {
      color: #fff;
    }
  }
}
</style>
